.FormInput {
	padding: 20px;
	max-width: 480px;
}

.FormInput__input {
	width: 100%;
	padding: 12px 20px;
	margin: 8px 0;
	display: inline-block;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-sizing: border-box;
	transition: background-color 0.5s ease-in-out;
}

.FormInput__input:focus {
	background-color: #e4ffe6;
}

.FormButton {
	width: 100%;
	background-color: green;
	color: white;
	font-size: 16px;
	padding: 8px 0px;
	margin: 8px 0;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	transition: background-color ease-in-out 300ms;
}

.FormButton:hover {
	background-color: #6EA31E;
}